










































































































































import Vue from 'vue'
import Header from '../components/Header.component.vue'
import echarts, { EChartsResponsiveOption, EChartOption } from 'echarts'
import { dutyService } from '../services/duty.service'
import { TopItem } from '../models/duty-top.model'

import moment from 'moment'
moment.locale('zh-CN')

let now = moment().format('M月D日，A，h点m分')
console.log('TCL: now', now)

const TOP_NUM = 10

export default Vue.extend({
  components: {},
  created() {
    this.loadTop()
  },
  data: function() {
    return {
      grade: '全部年级',
      gradeItems: [
        {
          label: '全部年级',
          value: '全部年级'
        },
        {
          label: '一年级',
          value: '一年级'
        },
        {
          label: '二年级',
          value: '二年级'
        },
        {
          label: '三年级',
          value: '三年级'
        },
        {
          label: '四年级',
          value: '四年级'
        },
        {
          label: '五年级',
          value: '五年级'
        },
        {
          label: '六年级',
          value: '六年级'
        }
      ],
      rating: 4,
      model: 'tab-1',
      headers: [
        {
          text: '名次',
          align: 'right',
          sortable: true,
          value: 'ranking'
        },
        {
          text: '班级',
          align: 'right',
          sortable: false,
          value: 'classFullName'
        },
        { text: '得分', align: 'right', sortable: true, value: 'amountScore' },
        { text: '星级', align: 'right', sortable: true, value: 'starLevel' }
      ],
      topItems: [] as TopItem[],
      allTopItems: [] as TopItem[],
      showAllTopBtn: true,
      showLoading: false,
      search: ''
    }
  },
  computed: {
    filteredTopItems() {
      const that: any = this

      if (that.grade === '全部年级') {
        return that.addRanking(that.topItems)
      }

      return that.addRanking(that.topItems).filter(e => e.grade === that.grade)
    }
  },
  methods: {
    onBack() {
      this.$router.push({
        name: 'home'
      })
    },
    loadTop() {
      this.showLoading = true
      dutyService
        .getDutyTop(1, 0, 1000)
        .then(res => {
          this.allTopItems = res.data.content
          this.topItems = this.allTopItems.slice(0, 10)
          this.showLoading = false
        })
        .finally(() => {
          this.showLoading = false
        })
    },
    viewAllTop() {
      this.topItems = this.allTopItems
      this.showAllTopBtn = false
    },
    addRanking(topItems: TopItem[]) {
      let items: {
        ranking: number
        classFullName: string
        amountScore: number
        starLevel: number
      }[] = []

      for (let index = 0; index < topItems.length; index++) {
        items.push({
          ...topItems[index],
          ranking: index + 1
        })
      }

      return items
    },
    showMedal(index: number) {
      return index === 1 || index === 2 || index === 3
    },
    medalClass(index: number) {
      let medalClass: string = ''

      switch (index) {
        case 1:
          medalClass = 'gold'
          break
        case 2:
          medalClass = 'sliver'
          break
        case 3:
          medalClass = 'copper'
          break
        default:
          break
      }

      return medalClass
    },
    formatRanking(ranking: number) {
      let title: string | number = ''

      switch (ranking) {
        case 1:
          title = '冠军'
          break
        case 2:
          title = '季军'
          break
        case 3:
          title = '亚军'
          break
        default:
          title = ranking
          break
      }

      return title
    }
  }
})
